import React from 'react'
import { makeStyles, Typography, Container, TextField, Grid, Link, Box, Button } from '@material-ui/core';
import { useFormik } from 'formik'
import * as yup from 'yup';
import Axios from 'axios';
import { useHistory } from 'react-router-dom'


const useStyles = makeStyles((theme) => ({
    root: {
        '& label.Mui-focused': {
            color: 'black',
        },

        '& .MuiOutlinedInput-root': {
            '& fieldset': {
                borderColor: '#92BBFF',
            },
            '&:hover fieldset': {
                borderColor: '#4188FF',
            },
            '&.Mui-focused fieldset': {
                borderColor: '#4188FF',
            },
        },
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '950px', 
        marginTop: theme.spacing(1),
        paddingLeft: '590px',
        color: 'red'

    },
    submit: {
        margin: theme.spacing(3, 0, 2),
        width: '50%',
        alignItems: 'center',
        backgroundColor: '#4188FF'

    },



    forgetpass: {
        paddingLeft: '240px',
        textDecorationColor: 'black'
    },
    btnsignin: {
        paddingLeft: '140px'
    },
    MuiTypographycolorPrimary: {
        alignItems: 'center',
        paddingLeft: '90px',
        color: '#4188FF !important',
        textDecorationColor: '#4188FF !important'

    },
    image: {
        paddingRight: '900px'
    },
    email: {
        outlineColor: 'red'
    },
    multilineColor: {
        borderWidth: "1px",
        borderColor: "#4188FF !important",
        color: '#4188FF'

    }


}));

function SignUp(props) {

    const classes = useStyles();
    let history = useHistory();


    function handleClick() {
        history.push("/");
    }


    const initState = {
        email: "",
        password: "",
        fullName: ""
    }

    const formik = useFormik({
        initialValues: { ...initState },
        onSubmit: async (values, action) => {
            sendDataToServer()
            console.log(values)



            let email = formik.values.email
            let fullName = formik.values.fullName
            let password = formik.values.password
            console.log('local email:', email)
            console.log('local fullName:', fullName)
            console.log('local password:', password)

            await Axios.post("http://localhost:8080/api/v1/auth/register/", {
                email,
                fullName,
                password
            }).then(res => {
                console.log('Response', res);
                console.log('Signed Up');
                alert('Signed Up')
            }).catch(err => {
                console.log('Error', err.response.data);
                alert('Error!')
            })

            action.resetForm();

        },
        validationSchema: yup.object({
            email: yup.string().email().required("This field is required"),
            password: yup.string()
                .min(6, "Password is too Short")
                .max(12, "Password is too long")
                .required("Password is Required"),
            fullName: yup.string().max(25, "Name is Too Long")

        })

    })


    const sendDataToServer = e => {

    }

    return (
        <div >
            <div className="">
                <div className="col-md-4 col-sm-4 logo text-center">
                    <img alt="logo" src={window.location.origin + "/images/logo/logo.svg"} />
                </div>
                <div className=" offset-md-8">

                </div>
            </div>
            <Container className="d-flex">

                <div className="col-md-6">

                </div>

                <div className=" d-flex flex-column mt-0z p-5 m-5">


                    <Typography component="h2" variant="h5" class="text-center">
                        SignUp Your Account.
                    </Typography>

                    <form className="d-flex flex-column my-4 py-4 p-3 justify-content-center" onSubmit={formik.handleSubmit}>


                        <TextField
                            className={classes.root + " my-4"}
                            id="fullName"
                            label="Full Name"
                            value={formik.values.fullName}
                            onChange={formik.handleChange}
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth

                            // autoFocus
                            InputProps={{
                                classes: {
                                    input: classes.multilineColor
                                }
                            }}
                        />
                        {formik.errors.fullName ? <div>{formik.errors.fullName}</div> : ""}
                        <TextField
                            className={classes.root + " my-4"}
                            id="email"
                            label="Email Address"
                            value={formik.values.email}
                            onChange={formik.handleChange}
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            name="email"
                            autoComplete="email"
                            InputProps={{
                                classes: {
                                    input: classes.multilineColor
                                }
                            }}
                        />
                        {formik.errors.email ? <div>{formik.errors.email}</div> : ""}

                        <TextField
                            className={classes.root}
                            label="Password"
                            value={formik.values.password}
                            onChange={formik.handleChange}
                            id="password"
                            required
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            type="password"
                            autoComplete="current-password"
                            InputProps={{
                                classes: {
                                    input: classes.multilineColor
                                }
                            }}
                        />

                        {formik.errors.password ? <div>{formik.errors.password}</div> : ""}




                        <Grid item xs className={classes.forgetpass}>
                            <Link href="#" variant="body2" style={{ color: 'black', paddingLeft: '120px' }}>
                                {/* <pre>         Sign In?</pre> */}Sign In
                            </Link>
                        </Grid>
                        <div className={classes.btnsignin}>
                            <Button
                                style={{ width: '166px', height: '56px', borderRadius: '16px', color: 'white', backgroundColor: '#4188FF', outlineStyle: 'none' }}
                                type="submit"
                                // fullWidth
                                variant="contained"
                                color="primary"
                                className={classes.submit}
                                onClick={formik.onSubmit}
                            >
                                SignUp
          </Button>
                        </div>

                    </form>

                    <Box mt={8} >
                        <Typography variant="body2" color="textSecondary" align="center">
                            {"Don't have an account ?"}
                            <Link color="primary" onClick={handleClick}>
                                Login
                     </Link>{' '}

                        </Typography>
                    </Box>
                </div>


            </Container>
        </div>
    )
}

export default SignUp
