import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import Mailchimp from './Mailchimp'
import Mailgun2 from './Mailgun2'
import CustomTab from './CustomTab'

const images = [
<img alt="mailchimp" src={window.location.origin + "/images/mailchimp.png"}/>,
<img alt="mailgun" src={window.location.origin + "/images/mailgun.png"}/>,
<img alt="twilio" src={window.location.origin + "/images/twilio.png"} />,
<img alt="silferbot" src={window.location.origin + "/images/silferbot.png"} />,
<img alt="activecampaign" src={window.location.origin + "/images/activecampaign.png"} />,
<img alt="activechat" src={window.location.origin + "/images/activechat.png"} />

]

function TabPanel(props) {
  const { children, value, index, ...other } = props;
  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`vertical-tabpanel-${index}`}
      aria-labelledby={`vertical-tab-${index}`}
      {...other}
    >
      {value === index && (
        <div>{children}</div>
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function a11yProps(index) {
  return {
    id: `vertical-tab-${index}`,
    'aria-controls': `vertical-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
    backgroundColor: theme.palette.background.paper,
    display: 'flex',
    // maxHeight: 224,
    // height: 400,
  },
  tabs: {
    height: 'calc(100vh - 120px)',
    borderRight: `1px solid ${theme.palette.divider}`,
  },
  tabPanel: {
    width:  '100%'
  }
}));

function VerticalTabs() {
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <>
    <div className="d-none d-sm-block">
    <div className={classes.root}>
      <Tabs
      style={{width:'242px',backgroundColor:'#FAFAFA'}}
        indicatorColor="primary"
        textColor="primary"
        orientation="vertical"
        variant="scrollable"
        value={value}
        onChange={handleChange}
        aria-label="Vertical tabs example"
        className={classes.tabs}
      >
        <Tab icon={<img alt="mailchimp" id="integration1" src={window.location.origin + "/images/mailchimpLogo.png"} />} {...a11yProps(0)} id="integration2" />
        <br /><br />
        <Tab icon={<img alt="mailgun" id="integration3" src={window.location.origin + "/images/mailgun 2.png"} />} {...a11yProps(1)} id="integration2" />
        <br /><br />
        <Tab icon={<img alt="twilio" id="integration4" src={window.location.origin + "/images/twilio.png"} />} {...a11yProps(2)} id="integration2" />
        <br /><br />
        <Tab icon={<img alt="silferbot" id="integration5" src={window.location.origin + "/images/silferbot.png"} />} {...a11yProps(3)} id="integration2" />
        <br /><br />
        <Tab icon={<img alt="activecampaign" id="integration6" src={window.location.origin + "/images/activecampaign.png"} />} {...a11yProps(4)} id="integration2" />
        <br /><br />
        <Tab icon={<img alt="activechat" id="integration7" src={window.location.origin + "/images/activechat.png"} />} {...a11yProps(5)} id="integration2" />
      </Tabs>

      <TabPanel value={value} index={0}>
        <Mailchimp />
      </TabPanel>
      <TabPanel value={value} index={1} />
      <TabPanel value={value} index={2} />
      <TabPanel  value={value} index={3}>
        {/* <Mailgun /> */}
        <Mailgun2/>
      </TabPanel>
      <TabPanel value={value} index={4} />
      <TabPanel value={value} index={5} />
    </div>
    </div>

    <div className=" d-lg-none d-sm-block">
      <CustomTab 
      className="mob_tab "
      tabClass="bg_mob_tab"
      labels={images}
      components={[<Mailchimp />,<Mailgun2/>]}
      />
    </div>
    </>
  );
}


export default VerticalTabs