import React from 'react'
import { useState } from 'react'
import { makeStyles, Typography, Container, TextField, Grid, Link, Box, Button, } from '@material-ui/core';
import { useFormik } from 'formik'
import { Auth } from '../../validations'
import { useHistory } from "react-router-dom"

const useStyles = makeStyles((theme) => ({
    root: {
        '& label.Mui-focused': {
            color: 'black',
        },

        '& .MuiOutlinedInput-root': {
            '& fieldset': {
                borderColor: '#92BBFF',
            },
            '&:hover fieldset': {
                borderColor: '#4188FF',
            },
            '&.Mui-focused fieldset': {
                borderColor: '#4188FF',
            },
        },
    },
    paper: {
        marginTop: theme.spacing(8),
        display: 'flex',
        flexDirection: 'column',
        alignItems: 'center',
    },
    avatar: {
        margin: theme.spacing(1),
        backgroundColor: theme.palette.secondary.main,
    },
    form: {
        width: '950px', 
        marginTop: theme.spacing(1),
        paddingLeft: '590px',
        color: 'red'

    },
    submit: {
        margin: theme.spacing(3, 0, 2),
        borderRadius: '14px',
        width: '50%',
        alignItems: 'center',
        backgroundColor: '#4188FF'


    },

    heading: {
        alignItems: 'center',
        width: '850px', 
        paddingLeft: '590px'
    },
    forgetpass: {
        paddingLeft: '240px',
        textDecorationColor: 'black'
    },
    btnsignin: {
        paddingLeft: '140px',
    },
    MuiTypographycolorPrimary: {
        alignItems: 'center',
        paddingLeft: '90px',
        color: '#4188FF !important',
        textDecorationColor: '#4188FF !important'

    },
    image: {
        paddingRight: '900px'
    },
    email: {
        outlineColor: 'red'
    },
    multilineColor: {
        borderWidth: "1px",
        borderColor: "#4188FF !important",
        color: '#4188FF'
    }


}));

function Login() {
    const classes = useStyles();

    const initState = {
        email: "",
        password: ""
    }
    let history = useHistory();

    function handleClickForLogin() {
        history.push("/main");
    }
    function handleClick() {
        history.push("/signup");
    }

    const [ErrorMsg] = useState(null)


    const formSubmit = async (values) => {

        handleClickForLogin()
    }

    const formik = useFormik({
        initialValues: { ...initState },
        onSubmit: formSubmit,
        validationSchema: Auth.LoginVld
    })

    return (
        <div >
            <div className="">
                <div className="col-md-4 col-sm-4 logo text-center">
                    <img alt="logo" src={window.location.origin + "/images/logo/logo.svg"} />
                </div>
                <div className=" offset-md-8">

                </div>
            </div>
            <Container className="d-flex">

                <div className="col-md-6">

                </div>

                <div className=" d-flex flex-column mt-0 p-5 m-5 h-100">


                    <Typography component="h2" variant="h5" className="text-center">
                        Sign in Your Account.
                    </Typography>


                    <form className="d-flex flex-column my-4 py-4 p-3 justify-content-center" onSubmit={formik.handleSubmit}>

                        <TextField
                            className={classes.root+" my-4"}
                            id="email"
                            name="email"
                            label="Email Address"
                            value={formik.values.email}
                            onChange={formik.handleChange}
                            variant="outlined"
                            margin="normal"
                            required
                            fullWidth
                            inputProps={{ onFocus: formik.handleBlur }}
                            error={formik.touched.email && formik.errors.email}
                            helperText={formik.touched.email && formik.errors.email ? formik.errors.email : ''}
                            InputProps={{
                                classes: {
                                    input: classes.multilineColor
                                }
                            }}

                        />

                        <TextField
                        style={{width:'410px',height:'60px',borderRadius:'10px'}}
                            id="password"
                            label="Password"
                            className={classes.root}
                            value={formik.values.password}
                            onChange={formik.handleChange}
                            required
                            variant="outlined"
                            margin="normal"
                            fullWidth
                            type="password"
                            autoComplete="current-password"
                            inputProps={{ onFocus: formik.handleBlur }}
                            error={formik.touched.password && formik.errors.password}
                            helperText={formik.touched.password && formik.errors.password ? formik.errors.password : ''}
                            InputProps={{
                                classes: {
                                    input: classes.multilineColor
                                }
                            }}
                        />


                        <Grid item xs className={classes.forgetpass}>
                            <Link variant="body2" style={{ color: 'black',outlineStyle:'none' }} className="ml-4">
                                Forgot password?
                            </Link>
                        </Grid>
                        <div className={classes.btnsignin}>
                            <Button
                                type="submit"
                                style={{width:'166px',height:'56px',borderRadius:'16px',color:'white',backgroundColor:'#4188FF',outlineStyle:'none'}}
                                color="primary"
                                className={classes.submit}>
                                Sign In
                            </Button>
                        </div>
                        <div className="">{ErrorMsg}</div>
                        <br></br>

                    </form>

                    <Box mt={8} className="align-text-bottom">
                        <Typography variant="body2" color="textSecondary" align="center">
                            {"Don't have an account ?"}
                            <Link color="primary" onClick={handleClick} >
                                Sign Up
                            </Link>
                        </Typography>
                    </Box>
                </div>
            </Container>
        </div>
    )
}

export default Login