import React from 'react';
import PropTypes from 'prop-types';
import { makeStyles, withStyles } from '@material-ui/core/styles';
import Tabs from '@material-ui/core/Tabs';
import Tab from '@material-ui/core/Tab';
import { ColorSchemeCode } from '../enums/ColorScheme';

const AntTabs = withStyles({
  root: {
    borderBottom: '1px solid #e8e8e8',
    minHeight: 0
  },
  indicator: {
    backgroundColor: ColorSchemeCode.activeTab,
  },
  flexContainer: {
    display: 'block'
  }
})(Tabs);

const AntTab = withStyles((theme) => ({
  root: {
    // ['@media (max-width:576px)']: {
    //   fontSize:'18px',
    // },
    textTransform: 'capitalize',
    fontWeight: 600,
    minWidth: 'fit-content',
    fontFamily: 'Poppins',
    padding: 0,
    minHeight: 0,
    marginLeft: '45px',
    lineHeight: 'normal',
    paddingBottom: '8px',
    fontSize: '24px',
    '&:hover': {
      color: ColorSchemeCode.activeTab,
      opacity: 1,
    },
    '&$selected': {
      color: ColorSchemeCode.activeTab,
    },
    '&:focus': {
      color: ColorSchemeCode.activeTab,
      outline: 'none'
    },
  },
  selected: {},
}))((props) => <Tab disableRipple {...props} />);

function TabPanel(props) {
  const { children, value, index, ...other } = props;

  return (
    <div
      role="tabpanel"
      hidden={value !== index}
      id={`scrollable-auto-tabpanel-${index}`}
      aria-labelledby={`scrollable-auto-tab-${index}`}
      {...other}
    >
      {value === index && (
        children
      )}
    </div>
  );
}

TabPanel.propTypes = {
  children: PropTypes.node,
  index: PropTypes.any.isRequired,
  value: PropTypes.any.isRequired,
};

function getProps(index) {
  return {
    id: `scrollable-auto-tab-${index}`,
    'aria-controls': `scrollable-auto-tabpanel-${index}`,
  };
}

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1
  },
}));

export default function CustomTab(props) {
  
  const classes = useStyles();
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <div className={classes.root}>
      <AntTabs
        value={value}
        onChange={handleChange}
          variant="scrollable"
          scrollButtons="auto"
          aria-label="scrollable auto tabs example"
          className={props.tabClass}
      >
        {props.labels.map((label, index) => (
          <AntTab key={index} className={props.className} label={label} {...getProps(index)} />
        ))} 
      </AntTabs>
      {props.components.map((component, index) => (
        <TabPanel value={value} key={index} index={index}>{component}</TabPanel>
      ))} 
    </div>
  );
}