import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import { ColorSchemeCode } from '../enums/ColorScheme'
import Button from '@material-ui/core/Button';

const getHoverColor = (color) => {
  switch (color) {
    case ColorSchemeCode.btn:
      return ColorSchemeCode.btnHover
  
    default:
      return color
  }
}


const getFocusColor = (color) => {
  switch (color) {
    case ColorSchemeCode.btn:
      return ColorSchemeCode.btnFocus
  
    default:
      return color
  }
}

const useStyles = makeStyles(theme => ({
  root: {
    backgroundColor: props => props.color,
    borderRadius: '16px',
    boxShadow: 'none',
    textTransform: 'capitalize',
    padding: '15px 20px',
    '&:hover': {
      backgroundColor: props => props.hover,
      boxShadow: 'none',
    },
    '&:focus': {
      backgroundColor: props => props.focus,
      boxShadow: 'none',
      outline: 'none'
    },
  },
}));

export default function CustomButton(props) {
  const propsObj = { 
    className : props.className, 
    hover     : getHoverColor(props.color),
    focus     : getFocusColor(props.color),
    ...props
  }
  const classes = useStyles(propsObj);
  const customClass = propsObj.className

  delete propsObj.className

  return <Button {...propsObj}  type="submit" variant="contained" color="primary" className={classes.root + ' ' + customClass}>
    {props.btntext}
  </Button>
}
